import _ from 'lodash';

const mutations = {
    setNav(state,data){
        state.nav = data
    },

    setUser(state, user) {
        var _user = _.merge({}, state.user);
        if (!_.size(_user)) {
            _user = {};
        }
        _.each(user, function (value, key) {
            _user[key] = value;
        });
        localStorage.setItem("user", JSON.stringify(_user));
        state.user = _user;
    },
    setToken(state, token) {
        localStorage.setItem("access_token", token);
        state.token = token;
    },
    LOGOUT(state) {
        localStorage.clear();
        state.user = {};
        state.token = null
    },
    worktabRemove (state, p) {
        // 关闭标签
        let ind = state.worktab.list.findIndex(s => s.name === p)
        if (ind > -1) {
            // 清理 keep alive - start
            state.closingPage = state.worktab.list[ind].name
            // 清理 keep alive - end
            state.worktab.list.splice(ind, 1)
        }
        if (p === state.worktab.current.name) {
            // 是当前页，返回上一页
            router.back()
        }
    },
    worktabRoute (state, p) {
        if (p.to.name){
            let ind = state.worktab.list.findIndex(s => s.name === p.to.name)
            if (ind > -1) {
                // 标签已存在
                state.worktab.current = state.worktab.list[ind]
            } else if (p.to){
                // 标签不存在，现在新建
                state.worktab.list.push(p.to)
                state.worktab.current = p.to
            }
            state.closingPage = ''
        }
    }
};
export  default mutations
