import Vue from 'vue'
import VueI18n from 'vue-i18n'
import enLocale from '../assets/en.json'
import zhLocale from '../assets/zh.json'

Vue.use(VueI18n)

const messages = {
    en: enLocale,
    zh: zhLocale
}

const index = new VueI18n({
    locale: 'zh',
    messages,
    silentTranslationWarn: true
})

export default index