const getters = {
    nav(state){
        return state.nav
    },
    user(state) {
        if (!state.user.username) {
            var user = {};
            var u = JSON.parse(localStorage.getItem("user"));
            if (u) {
                user = u;
            }
            state.user = user;
            return user;
        }
        return state.user
    },
    token(state) {
        if (!state.token) {
            var token = "";
            var u = localStorage.getItem("access_token");
            if (u) {
                token = u;
            }
            state.token = token;
            return token;
        }
        return state.token
    }
};
export  default  getters
