import request from '../utils/request'
import $ from 'jquery'

// 登录请求
const login = params => {
    return request.post('api/v1/auth/login', params).then(res => {
        return res.data
    })
};

// 登录请求
const checkCoupon = params => {
    return request.post('api/v1/checkCoupon', params).then(res => {
        return res.data
    })
};

// 检查邮件
const checkEmail = params => {
    return request.post('api/v1/checkEmail', params).then(res => {
        return res.data
    })
};

// 提交订单
const submitOrder = params => {
    return request.post('api/v1/submitOrder', params).then(res => {
        return res.data
    })
};

// 登录请求
const getProducts = params => {
    return request.get('api/v1/shop', params).then(res => {
        return res.data
    })
};

// 获取订单详情
const getInvoiceDetail = params => {
    return request.get('api/v1/order/info/'+params).then(res => {
        return res.data
    })
};

// 获取支付地址
const payInvoice = params => {
    return request.get('api/v1/order/pay/'+params).then(res => {
        return res.data
    })
};

// 获取支付地址
const getAllServices = params => {
    return request.get('api/v1/user/service', params).then(res => {
        return res.data
    })
};

// 获取用户信息
const getUserinfo = params => {
    return request.get('api/v1/user/info', params).then(res => {
        return res.data
    })
};

// 获取用户信息
const resetPassword = params => {
    return request.post('api/v1/user/reset/password', params).then(res => {
        return res.data
    })
};

// 查看账单列表
const getAllInvoices = params => {
    return request.get('api/v1/user/invoices', params).then(res => {
        return res.data
    })
};

// 获取文章列表
const getArticlesList = params => {
    return request.get('/assets/articles/list.json', {}).then(res => {
        return res
    })
};

// 激活预支付
const activePrepaid = params => {
    return request.post('api/v1/user/active/prepaid', params).then(res => {
        return res.data
    })
};

// 重置订阅
const resetSubscribe = params => {
    return request.post('api/v1/user/reset/subscribe', params).then(res => {
        return res.data
    })
};

const api = {
    login,
    checkCoupon,
    getProducts,
    checkEmail,
    submitOrder,
    getInvoiceDetail,
    payInvoice,
    getAllServices,
    getArticlesList,
    getUserinfo,
    resetPassword,
    getAllInvoices,
    activePrepaid,
    resetSubscribe,
};

export default api
