import Vue from 'vue';
import VueRouter from 'vue-router';
import {AU_AFF, AU_HMAC, AU_UUID} from "@/store/Global";

Vue.config.devtools = true;
Vue.use(VueRouter);

let vueRoutes = {
    saveScrollPosition: true,
    routes: [
        {
            path: '',
            name: 'service-panel',
            component: () => import('../views/PanelHome'),
            children: [
                {
                    name: 'home',
                    path: '/',
                    meta: {
                        title: '翻墙最快最稳妥 亚洲区最佳 VPN'
                    },
                    component: resolve => void (require(['../views/Home.vue'], resolve))
                },
                {
                    name: 'login',
                    path: '/user/login',
                    meta: {
                        title: '登录'
                    },
                    component: resolve => void (require(['../views/Login.vue'], resolve))
                },
                {
                    name: 'shop',
                    path: '/order/place',
                    meta: {
                        title: '订阅套餐'
                    },
                    component: resolve => void (require(['../views/Shop.vue'], resolve))
                },{
                    name: 'payment',
                    path: '/order/pay',
                    meta: {
                        title: '订单与服务'
                    },
                    component: resolve => void (require(['../views/PaymentPage.vue'], resolve))
                },{
                    name: 'invoiceDetail',
                    path: 'user/invoice/:invoiceId',
                    meta: {
                        title: '发票'
                    },
                    component: resolve => void (require(['../views/InvoiceDetail.vue'], resolve))
                },{
                    name: 'service',
                    path: 'user/service',
                    meta: {
                        title: '我的服务'
                    },
                    component: resolve => void (require(['../views/MyService.vue'], resolve))
                },{
                    name: 'account',
                    path: 'user/account',
                    meta: {
                        title: '管理账户'
                    },
                    component: resolve => void (require(['../views/ManageAccount.vue'], resolve))
                },{
                    name: 'manageBill',
                    path: 'user/invoice',
                    meta: {
                        title: '管理账单'
                    },
                    component: resolve => void (require(['../views/manageBill.vue'], resolve))
                },{
                    name: 'support',
                    path: '/support',
                    meta: {
                        title: '帮助中心 客户端配置 设置教程'
                    },
                    component: resolve => void (require(['../views/Support.vue'], resolve)),
                    children: [
                        {
                            path: 'article/:articleId',
                            name: 'article',
                            component: () => import('../components/SupportArticle.vue')
                        },
                        {
                            path: 'contact-us',
                            name: 'contact-us',
                            component: () => import('../components/SupportContactUsView.vue')
                        }
                    ]
                },
            ]
        }
    ]
};

const router = new VueRouter(vueRoutes);
router.beforeEach((to, from, next) => {
    if (to.query && to.query.aff) {
        localStorage.setItem(AU_AFF, to.query.aff);
    }
    if (to.path === '/' && to.query.hmac  && to.query.uuid){
        localStorage.setItem(AU_HMAC, to.query.hmac);
        localStorage.setItem(AU_UUID, to.query.uuid);
        localStorage.setItem('access_token', to.query.hmac);
        window.location.href = VUE_SITE;
    }
    document.title = `${to.meta.title} | ${SITE_NAME}`;
    next({query: to.query});
});
/*解决当前一个页面在底部打开下一个页面会停留在底部的问题*/
router.afterEach(() => {
    window.scrollTo(0, 0)
});
export default router;
